@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
/* https://wpblog.zyro.com/wp-content/uploads/2020/02/callie-schweitzer.png */
.App {
    text-align: center;
    font-family: 'Nunito', sans-serif;
    overflow: auto;
    padding-bottom: 5rem;
    height: 100vh;
}

@media screen and (max-width: 576px) {
    .container {
        margin-bottom: 5rem;
    }
    #tabHeader {
        margin-bottom: 1rem!important;
    }
}


/*Dark mode*/

@media (prefers-color-scheme: dark) {
    .App {
        background-color: #1a1a1a;
    }
    #tabHeader {
        color: #ddd;
    }
}
@media(prefers-color-scheme: dark){#loading>h1{color:#ddd}}
@media screen and (max-width: 576px){#bioDiv{display:block !important}#bio{text-align:center !important}}@media(prefers-color-scheme: dark){#bio-text{color:#ddd}}
#contact-container{border-radius:.5rem}@media screen and (max-width: 576px){#sendEmail{width:100%}}@media(prefers-color-scheme: dark){#contact-container{background-color:#ddd !important;border:1px solid #fff}#socials-header{color:#ddd}}
.nav-link{color:#a2a2a2 !important}.nav-link:hover{color:#000 !important;border-bottom:1px solid #ddd}.nav-link-focus{color:#000 !important;border-bottom:1px solid #ddd}@media screen and (max-width: 576px){#header{margin-top:3rem !important}#navBar{margin-top:1rem !important;margin-bottom:1rem !important}.nav-link h3{font-size:1.3rem;margin-top:-0.5rem;margin-bottom:0}.nav-link-focus{border-style:hidden}.nav-link:hover{border-style:hidden}.nav-link-focus h3{font-weight:bold}}@media(prefers-color-scheme: dark){#header{color:#ddd}.nav-link:hover{color:#ddd !important}.nav-link-focus h3{color:#fff}}
iframe{height:70rem}@media(prefers-color-scheme: dark){#home>*{color:#ddd}}
a{color:#000 !important;text-decoration:none !important}.card:hover{background-color:#e4e4e4}@media(prefers-color-scheme: dark){.card{background-color:#ddd !important}.card-body>span{color:#ddd}}
#github-icon{width:3rem}@media(prefers-color-scheme: dark){#source{color:#ddd}#github-icon{-webkit-filter:invert(1);filter:invert(1)}}
.skill-icon{height:6rem}.card>p{white-space:nowrap}
.language-icon{width:1rem;margin:0 .5rem}@media(prefers-color-scheme: dark){#selectLanguage>p{color:#ddd}}
